/*----------------------
  pullDown
 ---------------------*/

.anim-pullDown{
	animation-name: pullDown;
	-webkit-animation-name: pullDown;	

	animation-duration: 1.1s;	
	-webkit-animation-duration: 1.1s;

	animation-timing-function: ease-out;
	-webkit-animation-timing-function: ease-out;

	transform-origin: 50% 0%;
	-ms-transform-origin: 50% 0%;
	-webkit-transform-origin: 50% 0%;
}

@keyframes pullDown {
	0% {-webkit-transform: scaleY(0.1);transform: scaleY(0.1);}
	40% {-webkit-transform: scaleY(1.02);transform: scaleY(1.02);}
	60% {-webkit-transform: scaleY(0.98);transform: scaleY(0.98);}
	80% {-webkit-transform: scaleY(1.01);transform: scaleY(1.01);}
	100% {-webkit-transform: scaleY(0.98);transform: scaleY(0.98);}
	80% {-webkit-transform: scaleY(1.01);transform: scaleY(1.01);}
	100% {-webkit-transform: scaleY(1);transform: scaleY(1);}
}

@-webkit-keyframes pullDown {
	0% {-webkit-transform: scaleY(0.1);transform: scaleY(0.1);}
	40% {-webkit-transform: scaleY(1.02);transform: scaleY(1.02);}
	60% {-webkit-transform: scaleY(0.98);transform: scaleY(0.98);}
	80% {-webkit-transform: scaleY(1.01);transform: scaleY(1.01);}
	100% {-webkit-transform: scaleY(0.98);transform: scaleY(0.98);}
	80% {-webkit-transform: scaleY(1.01);transform: scaleY(1.01);}
	100% {-webkit-transform: scaleY(1);transform: scaleY(1);}
}

/*----------------------
  slideDown
 ---------------------*/
.anim-slideDown {
	animation-name: slideDown;
	-webkit-animation-name: slideDown;	

	animation-duration: 1s;	
	-webkit-animation-duration: 1s;

	animation-timing-function: ease;
	-webkit-animation-timing-function: ease;

	visibility: visible !important;
}

@keyframes slideDown {
	0% {-webkit-transform: translateY(-100%);transform: translateY(-100%);}
	50% {-webkit-transform: translateY(8%);transform: translateY(8%); }
	65% {-webkit-transform: translateY(-4%);transform: translateY(-4%);}
	80% {-webkit-transform: translateY(4%);transform: translateY(4%);}
	95%{-webkit-transform: translateY(-2%);transform: translateY(-2%);}
	100% {-webkit-transform: translateY(0%);transform: translateY(0%);}
}

@-webkit-keyframes slideDown {
	0% {-webkit-transform: translateY(-100%);transform: translateY(-100%);}
	50% {-webkit-transform: translateY(8%);transform: translateY(8%); }
	65% {-webkit-transform: translateY(-4%);transform: translateY(-4%);}
	80% {-webkit-transform: translateY(4%);transform: translateY(4%);}
	95%{-webkit-transform: translateY(-2%);transform: translateY(-2%);}
	100% {-webkit-transform: translateY(0%);transform: translateY(0%);}
}


/*----------------------
  slideRight
 ---------------------*/
.anim-slideRight{
	animation-name: slideRight;
	-webkit-animation-name: slideRight;	

	animation-duration: .5s;	
	-webkit-animation-duration: .5s;

	animation-timing-function: ease;
	-webkit-animation-timing-function: ease;

	visibility: visible !important;	
}

@keyframes slideRight {
	0% {-webkit-transform: translateX(-150%);transform: translateX(-150%);}
	50%{-webkit-transform: translateX(8%);transform: translateX(8%);}
	100% {-webkit-transform: translateX(0%);transform: translateX(0%);}
}

@-webkit-keyframes slideRight {
	0% {-webkit-transform: translateX(-150%);transform: translateX(-150%);}
	50%{-webkit-transform: translateX(8%);transform: translateX(8%);}
	100% {-webkit-transform: translateX(0%);transform: translateX(0%);}
}



/*----------------------
  slideUpExpand
 ---------------------*/
.anim-slideUpExpand {
	animation-name: slideUpExpand;
	-webkit-animation-name: slideUpExpand;

	animation-duration: 1.6s;
	-webkit-animation-duration: 1.6s;

	animation-timing-function: ease-out;
	-webkit-animation-timing-function: ease -out;

	visibility: visible !important;
}

@keyframes slideUpExpand {
	0% {-webkit-transform: translateY(100%) scaleX(0.5);transform: translateY(100%) scaleX(0.5);}
	30% {-webkit-transform: translateY(-8%) scaleX(0.5);transform: translateY(-8%) scaleX(0.5);}
	40% {-webkit-transform: translateY(2%) scaleX(0.5);transform: translateY(2%) scaleX(0.5);}
	50% {-webkit-transform: translateY(0%) scaleX(1.1);transform: translateY(0%) scaleX(1.1);}
	60% {-webkit-transform: translateY(0%) scaleX(0.9);transform: translateY(0%) scaleX(0.9);}
	70% {-webkit-transform: translateY(0%) scaleX(1.05);transform: translateY(0%) scaleX(1.05);}
	80% {-webkit-transform: translateY(0%) scaleX(0.95);transform: translateY(0%) scaleX(0.95);}
	90% {-webkit-transform: translateY(0%) scaleX(1.02);transform: translateY(0%) scaleX(1.02);}
	100% {-webkit-transform: translateY(0%) scaleX(1);transform: translateY(0%) scaleX(1);}
}

@-webkit-keyframes slideUpExpand {
	0% {-webkit-transform: translateY(100%) scaleX(0.5);transform: translateY(100%) scaleX(0.5);}
	30% {-webkit-transform: translateY(-8%) scaleX(0.5);transform: translateY(-8%) scaleX(0.5);}
	40% {-webkit-transform: translateY(2%) scaleX(0.5);transform: translateY(2%) scaleX(0.5);}
	50% {-webkit-transform: translateY(0%) scaleX(1.1);transform: translateY(0%) scaleX(1.1);}
	60% {-webkit-transform: translateY(0%) scaleX(0.9);transform: translateY(0%) scaleX(0.9);}
	70% {-webkit-transform: translateY(0%) scaleX(1.05);transform: translateY(0%) scaleX(1.05);}
	80% {-webkit-transform: translateY(0%) scaleX(0.95);transform: translateY(0%) scaleX(0.95);}
	90% {-webkit-transform: translateY(0%) scaleX(1.02);transform: translateY(0%) scaleX(1.02);}
	100% {-webkit-transform: translateY(0%) scaleX(1);transform: translateY(0%) scaleX(1);}
}


/*----------------------
  shake
 ---------------------*/
 .anim-shake{
 	animation-name: shake;
 	-webkit-animation-name: shake;

 	animation-duration: .3s;
 	-webkit-animation-duration: .3s;

 	animation-timing-function: ease;
 	-webkit-animation-timing-function: ease;
 
 	-ms-transform-origin: 50% 100%;
 
 	    transform-origin: 50% 100%;
 	-webkit-transform-origin: 50% 100%;
 }

 @keyframes shake {
 	0% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	10% {-webkit-transform: translateY(-80%) scaleY(1) scaleX(1);transform: translateY(-80%) scaleY(1) scaleX(1);}
 	15% {-webkit-transform: translateY(-40%) scaleY(1) scaleX(1);transform: translateY(-40%) scaleY(1) scaleX(1);}
 	20% {-webkit-transform: translateY(-20%) scaleY(1) scaleX(1);transform: translateY(-20%) scaleY(1) scaleX(1);}
 	30% {-webkit-transform: translateY(40%) scaleY(1) scaleX(1);transform: translateY(40%) scaleY(1) scaleX(1);}
 	40% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	50% {-webkit-transform: translateY(40%) scaleY(1) scaleX(1);transform: translateY(40%) scaleY(1) scaleX(1);}
 	60% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	70% {-webkit-transform: translateY(40%) scaleY(1) scaleX(1);transform: translateY(40%) scaleY(1) scaleX(1);}
 	80% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	90% {-webkit-transform: translateY(25%) scaleY(1) scaleX(1);transform: translateY(25%) scaleY(1) scaleX(1);}
 	100% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 }

 @-webkit-keyframes shake {
 	0% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	10% {-webkit-transform: translateY(-80%) scaleY(1) scaleX(1);transform: translateY(-80%) scaleY(1) scaleX(1);}
 	15% {-webkit-transform: translateY(-40%) scaleY(1) scaleX(1);transform: translateY(-40%) scaleY(1) scaleX(1);}
 	20% {-webkit-transform: translateY(-20%) scaleY(1) scaleX(1);transform: translateY(-20%) scaleY(1) scaleX(1);}
 	30% {-webkit-transform: translateY(40%) scaleY(1) scaleX(1);transform: translateY(40%) scaleY(1) scaleX(1);}
 	40% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	50% {-webkit-transform: translateY(40%) scaleY(1) scaleX(1);transform: translateY(40%) scaleY(1) scaleX(1);}
 	60% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	70% {-webkit-transform: translateY(40%) scaleY(1) scaleX(1);transform: translateY(40%) scaleY(1) scaleX(1);}
 	80% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 	90% {-webkit-transform: translateY(25%) scaleY(1) scaleX(1);transform: translateY(25%) scaleY(1) scaleX(1);}
 	100% {-webkit-transform: translateY(0%) scaleY(1) scaleX(1);transform: translateY(0%) scaleY(1) scaleX(1);}
 }


 /*----------------------
   zoomIn
  ---------------------*/
 .anim-zoomIn{
 	animation-name: zoomIn;
 	-webkit-animation-name: zoomIn;

 	animation-duration: 1.6s;
 	-webkit-animation-duration: 1.6s;

 	animation-timing-function: ease-out;
 	-webkit-animation-timing-function: ease-out;

 	visibility: visible !important;
 }

 @keyframes zoomIn {
 	0% {-webkit-transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);}
 	30% {-webkit-transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);}
 	45% {-webkit-transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);}
 	60% {-webkit-transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);}
 	75% {-webkit-transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);}
 	90% {-webkit-transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);}
 	100% {-webkit-transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);}
 }

 @-webkit-keyframes zoomIn {
 	0% {-webkit-transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);transform: scale(0.3) rotate(6deg) translateX(-30%) translateY(30%);}
 	30% {-webkit-transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);transform: scale(1.03) rotate(-2deg) translateX(2%) translateY(-2%);}
 	45% {-webkit-transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);transform: scale(0.98) rotate(1deg) translateX(0%) translateY(0%);}
 	60% {-webkit-transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);transform: scale(1.01) rotate(-1deg) translateX(0%) translateY(0%);}
 	75% {-webkit-transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);transform: scale(0.99) rotate(1deg) translateX(0%) translateY(0%);}
 	90% {-webkit-transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);transform: scale(1.01) rotate(0deg) translateX(0%) translateY(0%);}
 	100% {-webkit-transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);transform: scale(1) rotate(0deg) translateX(0%) translateY(0%);}
 }



