/*----------------
  Nav
 */

 .Nav {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-bottom: 1rem;
	width: 100vw;
 }

 .Nav div {
	background: #497ADB;
	border: 1px solid #ddd;
	border-top: none;
	color: white;
	font-size: 1.5rem;
	padding: 1em;
 }

 .Nav div:hover {
	background: #ccc;
	color: #000;
 }

 .Nav div:active {
	background: #999;
	color: #fff;
 }




